<template>
  <div>
    <h1 class="titulo">Welcome to our gallery!</h1>
    <h3 class="subtitulo">
      Here you will find some of the best projects carried out in our community
    </h3>
    <br />
    <div class="container-md">
      <b-card-group columns>
        <div columns v-for="proyecto in proyectos" :key="proyecto.id">
          <CardProyecto
            :tags="proyecto.tags"
            :autor="proyecto.autor"
            :srcimg="proyecto.srcimg"
            :alt="proyecto.alt"
            :titulo="proyecto.titulo"
            :texto="proyecto.texto"
            :link="proyecto.link"
            :fecha="proyecto.fecha"
          ></CardProyecto>
        </div>
      </b-card-group>
    </div>
    <br />
    <Futer />
  </div>
</template>

<script>
  import CardProyecto from "../components/CardProyecto.vue";
  import Futer from "../components/Futer.vue";
  const lstProysJson = "../ListaProyectos.json";

  export default {
    props: {
      name: {
        type: String,
        default: "Galeria",
      },
    },
    
    components: {
      CardProyecto,
      Futer,
    },
    data() {
      return {
        proyectos: [],
        githubPage: 1,
        page: 1,
        loading: false,
        perPage: 7,
      };
    },
    mounted() {
      this.fetchData();
    },

    methods: {
      async fetchData() {
        const res = await fetch(lstProysJson);
        const val = await res.json();
        this.proyectos = val;
        console.log(val);
      },
    },
  };
</script>
