<template>
  <div class="tarjetaProyecto">
    <b-card no-body class="card border-0 rounded-0 mb-3">
      <template #header>
        <div>{{ nombre }}</div>
      </template>
      <b-card-body>

     <b-carousel
      id="carousel-1"
      v-model="slide"
      :interval="8000"
      controls
      indicators
      background="#ababab"
      img-width="1024"
      img-height="480"
      style="text-shadow: 1px 1px 2px #333;"
      @sliding-start="onSlideStart"
      @sliding-end="onSlideEnd"
    >
      <b-carousel-slide
          :text="texto"
          :img-src="foto1"
          :img-alt="alt1"
      ></b-carousel-slide>

      <b-carousel-slide
          :text="texto"
          :img-src="foto2"
          :img-alt="alt2"
      ></b-carousel-slide>

      <b-carousel-slide
          :text="texto"
          :img-src="foto3"
          :img-alt="alt3"
      ></b-carousel-slide>


        <b-card-title>{{ ubicacion }}</b-card-title>
        <b-card-sub-title class="mb-2">{{ fecha }}</b-card-sub-title>
        <b-card-text>
          {{ texto }}
        </b-card-text>
        
     </b-carousel>

          <b-card-text >
     {{descripcion}} 
    </b-card-text>

      </b-card-body>



    </b-card>
  </div>
</template>

<script>
export default {
  props: [
    "id",
    "nombre",
    "descripcion",
    "fecha",
    "texto",
    "ubicacion",
    "foto1",
    "alt1",
    "foto2",
    "alt2",
    "foto3",
    "alt3",
    "foto4",
    "alt4",
    "foto5",
    "alt5"
  ],
};
</script>