<template>
  <div>
    <h1 class="titulo">Join now our community!</h1>
    <h3 class="subtitulo">
      A world full of creativity awaits you in EGO Fan Creators
    </h3>
    <b-form @submit="onSubmit" @reset="onReset" v-if="show" class="formulario">
      <b-form-group
        id="input-group-1"
        label="Email address:"
        label-for="input-1"
      >
        <b-form-input
          id="input-1"
          v-model="form.email"
          type="email"
          placeholder="Enter email"
          required
        ></b-form-input>
      </b-form-group>

      <b-form-group id="input-group-2" label="Your Name:" label-for="input-2">
        <b-form-input
          id="input-2"
          v-model="form.name"
          placeholder="Enter name"
          required
        ></b-form-input>
      </b-form-group>

      <b-form-group id="input-group-3" label="Your age:" label-for="input-3">
        <b-form-select
          id="input-3"
          v-model="form.food"
          :options="ages"
          required
        ></b-form-select>
      </b-form-group>

      <b-form-group id="input-group-4" v-slot="{ ariaDescribedby }">
        <b-form-checkbox-group
          v-model="form.checked"
          id="checkboxes-4"
          :aria-describedby="ariaDescribedby"
        >
          <b-form-checkbox value="mailInfo=true" class="marcable"
            >I'd like to recieve info of nearby events on my
            email</b-form-checkbox
          >
        </b-form-checkbox-group>
      </b-form-group>

      <b-button class="botones" type="submit" variant="secondary"
        >Submit</b-button
      >
      <b-button type="reset" variant="secondary">Reset</b-button>
    </b-form>
    <br />
    <Futer />
  </div>
</template>

<script>
import Futer from "@/components/Futer.vue";
export default {
  components: {
    Futer,
  },
  data() {
    return {
      form: {
        email: "",
        name: "",
        food: null,
        checked: [],
      },
      ages: [
        { text: "Select your age", value: null },
        "-18 years (with the representation of an adult)",
        "18-24",
        "25-35",
        "36-50",
        "51-65",
        "+65",
      ],
      show: true,
    };
  },
  methods: {
    onSubmit(event) {
      event.preventDefault();
      alert(JSON.stringify(this.form));
    },
    onReset(event) {
      event.preventDefault();
      // Reset our form values
      this.form.email = "";
      this.form.name = "";
      this.form.food = null;
      this.form.checked = [];
      // Trick to reset/clear native browser form validation state
      this.show = false;
      this.$nextTick(() => {
        this.show = true;
      });
    },
  },
};
</script>