import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Galeria from '../views/Galeria.vue'
import Eventos from '../views/Eventos.vue'
import Join from '../views/Join.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/Galeria',
    name: 'Galeria',
    component: Galeria
  },
  {
    path: '/Eventos',
    name: 'Eventos',
    component: Eventos
  },
  {
    path: '/Join',
    name: 'Join',
    component: Join
  },
]

const router = new VueRouter({
  mode: "history",
  base: '/EGP_Fan_Creators/',
  routes
})

export default router
